import * as Yup from 'yup';

export const normalLoginState = {
  email: 'ldtan89@gmail.com',
  password: '12345',
};

export const normalLoginConfig = [
  {
    name: 'phone',
    key: 'phone',
    label: 'Số điện thoại',
    required: true,
    props: {
      className: 'login_form_phone_number',
      placeholder: 'Số điện thoại',
      inputProps: { maxLength: 10 },
    },
  },
  {
    name: 'password',
    key: 'password',
    label: 'Mật khẩu',
    required: true,
    props: {
      placeholder: 'Mật khẩu',
      type: 'password',
      className: 'login_form_password',
    },
  },
];

export const loginValidateSchema = Yup.object().shape({
  phone: Yup.string()
    .matches(
      /(03|05|07|08|09|01[2|6|8|9])+([0-9]{8})\b/,
      'Số điện thoại không hợp lệ'
    )
    .required('Vui lòng nhập số điện thoại'),
  password: Yup.string()
    .min(6, 'Mật khẩu phải ít nhất 6 kí tự')
    .required('Chưa nhập mật khẩu'),
});
